"use client";

import { useState } from "react";
import axios from "axios";
import { motion } from "framer-motion";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";

export default function Home() {
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<any>(null);

  const handleSubmit = async () => {
    setLoading(true);
    try {
      const res = await axios.post(
        "https://fraud-api.up.railway.app/predict", // replace with real API
        { text }
      );
      setResult(res.data);
    } catch (err) {
      console.error(err);
      setResult({ error: "Something went wrong. Try again!" });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-600 to-blue-800 flex flex-col items-center text-white">
      {/* Hero */}
      <motion.div
        initial={{ opacity: 0, y: -30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.7 }}
        className="mt-20 text-center max-w-2xl"
      >
        <h1 className="text-5xl font-extrabold mb-4">
          Fraud Job Posting Detector
        </h1>
        <p className="text-lg opacity-80">
          Paste a job description and let AI tell you if it‚Äôs legitimate or fake.
        </p>
      </motion.div>

      {/* Form */}
      <motion.div
        initial={{ opacity: 0, y: 30 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="mt-12 w-full max-w-2xl"
      >
        <Card className="bg-white/10 backdrop-blur-lg rounded-2xl shadow-lg">
          <CardContent className="p-6">
            <Textarea
              placeholder="Paste job description here..."
              className="w-full text-black rounded-lg mb-4"
              rows={6}
              value={text}
              onChange={(e) => setText(e.target.value)}
            />
            <Button
              onClick={handleSubmit}
              disabled={loading}
              className="w-full py-3 text-lg rounded-xl bg-purple-500 hover:bg-purple-600"
            >
              {loading ? "Analyzing..." : "Detect Fraud"}
            </Button>
          </CardContent>
        </Card>
      </motion.div>

      {/* Result */}
      {result && (
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
          className="mt-10 w-full max-w-2xl"
        >
          <Card className="bg-white/20 backdrop-blur-md rounded-2xl shadow-lg">
            <CardContent className="p-6 text-center">
              {result.error ? (
                <p className="text-red-300 font-bold">{result.error}</p>
              ) : (
                <>
                  <h2 className="text-2xl font-semibold mb-2">
                    Prediction Result
                  </h2>
                  <p className="text-xl">
                    {result.prediction === 1 ? "üö® Fraudulent" : "‚úÖ Legitimate"}
                  </p>
                  <p className="mt-2 text-sm opacity-70">
                    Confidence: {(result.probability * 100).toFixed(2)}%
                  </p>
                </>
              )}
            </CardContent>
          </Card>
        </motion.div>
      )}

      {/* Footer */}
      <footer className="mt-20 mb-6 text-sm opacity-70">
        Made with ‚ù§Ô∏è by Manthan |{" "}
        <a
          href="https://your-resume-link.com"
          className="underline hover:text-purple-300"
        >
          Resume
        </a>{" "}
        |{" "}
        <a
          href="https://github.com/yourusername/fraud-detector"
          className="underline hover:text-purple-300"
        >
          GitHub
        </a>
      </footer>
    </div>
  );
}
